import Vue from 'vue'
import App from './App'
import * as VueGoogleMaps from 'vue2-google-maps'

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyDs9gSvMR-sRF9WMJpDyqOwUgWkFXqWcPw',
    libraries: 'places' // necessary for places input
  }
})

const app = new Vue({
  el: '#app',
  components: {
    App
  },
  template: '<App/>'
})

app.$mount('#app')



